<template>
  <el-tabs type="card">
    <el-tab-pane label="Alarm Alert">
      <alarm-alert />
    </el-tab-pane>
    <el-tab-pane label="History">
      <history-case />
    </el-tab-pane>
    <el-tab-pane label="Flow">
      <flow />
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import AlarmAlert from '@/components/Monitoring/LOS/AlarmAlert'
import HistoryCase from '@/components/Monitoring/LOS/HistoryCase'
import Flow from '@/components/Monitoring/LOS/Flow'

export default {
  components: {
    AlarmAlert,
    HistoryCase,
    Flow
  },

  data() {
    return {
      tabPosition: 'left'
    }
  }
}
</script>
<style scoped lang="css">
  >>>.el-tabs__item {
    font-weight: bold;
  }
  .wrapper > .main > .content > .inner {
    margin: 1.25rem 0;
  }
  >>>.el-tabs__content {
    margin: 0 1rem;
  }
  >>>.el-tabs__nav {
    margin-left: 1rem;
  }
</style>



